.backhome {
    background-color: #0E2337;
    height: 3.3rem;
    border-radius: 0.8rem;
    cursor: pointer;
}

.backhome:hover {
    background-color: #66B2FF;
}

.backhome:hover .home-icon {
    color: #0E2337;
}

.home-icon {
    padding-top: 0.7rem;
    padding-left: 0.2rem;
}

.hero-holder {
    background-color: #0E2337;
    height: 6rem;
    width: 16rem;
    border-radius: 1.2rem;
    margin-top: 1.4rem;
    display: flex;
    align-items: center;
}

.hero-icon-holder {
    height: 3rem;
    width: 3.4rem;
    border-radius: 3rem;
    margin-left: 1rem;
}

.hero-icon {
    padding-top: 0.6rem;
    padding-left: 0.1rem;
}

.even-row {
    background-color: #0c2031;
}

.dldata-pik{
    width: 3rem;
}
  