.Dashboard-Side {
    background-color: #0f263f;
    padding: 3rem 1rem;
}

.bulletin-record {
    padding-top: 0.3rem;
    height: 300px;
    width: 320px;
    overflow-y: scroll;
}

.bulletin-record::-webkit-scrollbar {
    display: none;
}

.bulletin-holder {
    margin-left: 2rem;
    background-color: #e3755f;
    height: 95px;
    border-radius: 1.5rem;
    margin-bottom: 0.5rem;
    font-family: 'Sk-Modernist-Regular';
    display: flex;
    justify-content: center;
    align-items: center;
}

.notification-history {
    margin-left: 2rem;
    background-color: #15314e;
    height: 95px;
    border-radius: 1.5rem;
    margin-top: 0.9rem;
    font-family: 'Sk-Modernist-Regular';
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
}

.realm-not {
    font-size: 0.7rem;
}

.warning-holder {
    display: flex;
    flex-direction: column;
}

.warning-signal {
    font-weight: 600;
    font-family: "Sk-Modernist-Regular";
    letter-spacing: 0.1rem;
    font-size: 1rem;
}

.warning-icon {
    padding-right: 1rem;
}

.subwarning {
    font-size: 0.6rem;
    width: 200px;
    line-height: 10px; 
}

.bulletin-holder:hover {
    background-color: #16334e;
    cursor: pointer;
}

.bulletin-holder:hover .subwarning {
    color: white;
}

.bulletin-parameter {
    font-weight: 600;
    font-size: 1rem;
    text-transform: uppercase;
    font-family: 'Inter';
}

.bulletin-status > .a {
    font-size: 1rem;
    white-space: nowrap; 
    overflow: hidden;
    padding-left: 7.2rem;
}

.bulletin-time {
    padding-left: 6rem;
    font-size: 1rem;
    color: #ffff;
}


.alert-holder {
    overflow-y: scroll;
    overflow-x: hidden;
    text-align: left;
    padding: 1rem;
}

.alert-holder::-webkit-scrollbar {
    width: 10px;
}

.alert-holder:-webkit-scrollbar-track {
    background-color: #122e4d;
}

.alert-holder::-webkit-scrollbar-thumb {
    background-color: #508ecc;
    border-radius: 3rem;
}

.alert-details {
    background-color: #122e4d;
    height: 90px;
    width: 400px;
    border-radius: 1rem;
    margin-bottom: 0.5rem;
    border: none;
    font-family: 'Sk-Modernist-Regular';
    text-align: left;
    color: white;
}

.param-name {
    font-weight: 500;
    font-size: 1rem;
    text-transform: uppercase;
    padding-left: 2.2rem;
    font-family: 'Inter';   
}

.status > .a {
    font-size: 0.7rem;
    width: 503px;
    text-transform: uppercase;
    padding-left: 2.2rem;
    color: #ff725e;
}

.time-created {
    padding-left: 2.2rem;
    font-size: 0.7rem;
    color: #508ecc;
    padding-top: 1rem;
}

.content-holder {
    background: rgba(15, 38, 63, 0.54);
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(5.7px);
    -webkit-backdrop-filter: blur(5.7px);
}

.alert-title {
    background: rgba(15, 38, 63, 0.54);
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(5.7px);
    -webkit-backdrop-filter: blur(5.7px);
    color: white;
    border-top-right-radius: 20px;
    border-top-left-radius: 20px;
    height: 50px;
}

.modal-holder {
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.warn-icon {
    animation-name: example;
    animation-duration: 1s;
    animation-fill-mode: forwards;
    animation-iteration-count: 1;
}

@keyframes example {
    0% {transform: scale(1);}
    50% {transform: scale(1.2);}
    100% {transform: scale(1);}
}